.gpt3__navbar{
    display: flex;
   /* border: 1px solid white; */
 
    justify-content: space-between;
    align-items: center; 
    padding:2rem 6rem; 
}
.gpt3__navbar-links{
    /* setting up flex:grow,flex:shrink,flex:basis all to 1 simulataneously */
   flex:1;
   display: flex;
     justify-content: flex-start;
     align-items: center;
}
.gpt3__navbar-links_logo{
    margin-right: 2rem;
}
.gpt3__navbar-links_logo img{
    width: 62.56px;
    height: 16.02px;
}
.gpt3__navbar-links_container{
    display:flex;
    flex-direction: row;
}
.gpt3__navbar-sign{

    display: flex;
    /* justify-content: flex-end; */
    align-items: center;

}
.gpt3__navbar-links_container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p{
    color: #fff;
    font-family: var(--font-family);
    font-weight:500 ;
    font-size: 18px;
    line-height: 25px;
    text-transform:capitalize ;
    margin:0 1rem;
    cursor:pointer;
    /* text-decoration: none; */
}





.gpt3__navbar-sign p,
.gpt3__navbar-sign button
{
    margin: 0 1rem;
}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button
{
    padding: 0.5rem 1rem;
    color: #fff;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border-radius: 5px;
    border: 0;
    outline: none;
    cursor: pointer;
    
}
.gpt3__navbar-menu{
    margin-left: 1rem;
    display: none;
    position: relative;
    /* border: 1px solid yellow; */
}
.gpt3__navbar-menu svg{
    cursor: pointer;
}
.gpt3__navbar-menu_container{
    /* border: 1px solid white; */
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background:var(--color-footer) ;
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width:210px ;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
}
.gpt3__navbar-links_container {
    /* border: 1px solid pink; */
    
    text-decoration: none;


}
.gpt3__navbar-menu_container p{
    margin: 1rem 0;
}
.gpt3__navbar-menu_container-links-sign{
    display: none;
}
/* smaller screens */
@media screen and (max-width:1050px)
{
.gpt3__navbar-links_container{
display: none;
}
.gpt3__navbar-menu{
    display: flex;
    
}
}
a{
    text-decoration: none;
}
/* tablet devices */
@media screen and (max-width:700px){
    /* push the navbar up a bit and centered */
   .gpt3__navbar{
       padding:2rem 4rem;

   } 
}
/*mobile devices  */
@media screen and (max-width:550px){
    /* push the navbar up a bit and centered */
   .gpt3__navbar{
       padding:2rem ;
   } 
   /* remove the sign in buttons for mobile devices for a cleaner look */
   .gpt3__navbar-sign{
       display: none;
   }
   .gpt3__navbar-menu_container{
    top: 20px;
}
.gpt3__navbar-menu_container-links-sign{
    display: block;
}
}




