/* to all the elements */
*{
box-sizing: border-box;
padding: 0;
margin: 0;
scroll-behavior: smooth;
}
body{
    background-color: var(--color-bg);
}
/* gradients example website: angrytools.com */
a{
    color: unset;
    
}
.gradient__bg{
    background: radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
}
.gradient__text{
    background: var(--gradient-text);
    /* background-color: text; */
    /* webkit for it to work on all browsers */
    /* these two texts are used in combination... 'clip' allows the bg to appear only within
     the text content and 'fill' effectively makes the bg transparent. */
     -webkit-background-clop: text;
     background-clip: text;
     -webkit-text-fill-color: transparent;
     color: white;
}
    
.section__padding{
    padding:4rem 6rem;
}
.section__margin{
    margin:4rem 6rem;
}
/* website to set animations */
/* animista.net */
/* copy the code lol of the kind of animation u want*/
.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

 @-webkit-keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  
@media screen and (max-width:700px){
    .section__padding{
        padding: 4rem;
    }
    .section__margin{
margin: 4rem;
    }
}
@media screen and (max-width:550px){
    .section__padding{
        padding: 4rem 2rem;
    }
    .section__margin{
margin: 4rem 2rem;
    }
}